<template>
  <header id="headerTab">
    <p>{{headertitle}}</p>
  </header>
</template>

<script>
  export default {
    props: ['headertitle']
  }
</script>

<style lang="less" scoped>
  @import '../../../static/less/base.less';
  header {
    height: .8rem;
    text-align: center;
    background: @theme_background;
    color: @base_color;
    font-size: .26rem;
    line-height: .8rem;
    width: 100%;
    z-index: 10;
    position: absolute;
    top: 0;
  }
</style>


